<template>
  <layout>
    <template v-slot:content>
      <main class="dashboard container section">
        <dashboard-search></dashboard-search>
        <div class="dashboard__sort">
          <span class="dashboard__sort-text">Sort by:</span> <dashboard-sort></dashboard-sort>
        </div>
        <q-list
          v-if="sortedStarships.length > 0"
          class="dashboard__list"
          :class="fetching ? 'dashboard__list--fetching' : ''"
          dark
          bordered>
          <q-expansion-item
            v-for="(starship, index) in sortedStarships"
            :key="`${starship.name}-${index}`"
            group="starshipItems"
            :label="starship.name">
            <dashboard-item :starship="starship"></dashboard-item>
          </q-expansion-item>
          <Loader v-if="fetching"></Loader>
        </q-list>
        <p class="text-h5" v-if="sortedStarships.length <= 0 && fetching">Loading starships...</p>
        <q-ajax-bar
          position="bottom"
          color="secondary"
          size="10px"/>
      </main>
    </template>
  </layout>
</template>

<script>
  import { mapActions, mapGetters, mapState } from 'vuex';
  import Layout from '@/app/common/layout/Layout';
  import Loader from '@/app/common/ui/Loader';
  import DashboardSearch from './DashboardSearch';
  import DashboardSort from './DashboardSort';
  import DashboardItem from './DashboardItem';

  export default {
    name: 'DashboardMain',
    components: {
      Layout,
      Loader,
      DashboardSearch,
      DashboardSort,
      DashboardItem,
    },
    computed: {
      ...mapGetters(['sortedStarships', 'starshipsMeta', 'currentPage']),
      ...mapState({
        fetching: state => state.fetching,
        sortDirection: state => state.Dashboard.sortDirection,
      }),
    },
    created() {
      this.fetchData();
      this.fetchInterval = setInterval(() => {
        this.clearStarshipsData();
        this.fetchData();
      }, 60000);
    },
    beforeDestroy () {
      clearInterval(this.$options.fetchInterval);
    },
    methods: {
      ...mapActions([
        'fetchStarships',
        'clearStarships',
        'clearStarshipsMeta',
        'resetPage',
      ]),
      async fetchData() {
        if (this.starshipsMeta
          && Object.entries(this.starshipsMeta).length === 0
          && this.starshipsMeta.constructor === Object) {
          await this.fetchStarships({ page: 1 });
          await this.fetchData();
        }
        if (this.currentPage <= this.starshipsMeta.lastPage) {
          const params = {
            page: this.currentPage,
          };
          await this.fetchStarships(params);
          await this.fetchData();
        }
        return null;
      },
      clearStarshipsData() {
        this.clearStarships();
        this.clearStarshipsMeta();
        this.resetPage();
        return null;
      },
    }
  };
</script>

<style lang="scss" scoped>

  .dashboard__sort {
    margin-bottom: 1.5rem;
  }

  .dashboard__sort-text {
    margin-right: 1.25rem;
    font-size: 1.125rem;
  }

  .dashboard__list {
    margin-bottom: 1.5rem;

    &--fetching {
      position: relative;

      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 0.6;
        background-color: var(--white);
        pointer-events: none;
      }
    }
  }
</style>
