<template>
  <q-card class="bg-grey-7">
    <q-card-section>
      <section class="dashboard__item">
        <h2 class="hidden">Starship specs</h2>
        <div class="dashboard__item-description">
          <p>Model: {{ starship.model }}</p>
          <p>Starship class: {{ starship.starship_class }}</p>
          <p>Manufacturer: {{ starship.manufacturer }}</p>
          <p>Cost: {{ starship.cost_in_credits }}</p>
          <p>Length: {{ starship.length }}</p>
          <p>Crew capacity: {{ starship.crew }}</p>
          <p>Passengers capacity: {{ starship.passengers }}</p>
          <p>Max speed: {{ starship.max_atmosphering_speed }}</p>
          <p>Hyperdrive rating: {{ starship.hyperdrive_rating }}</p>
          <p>Speed, Megalights/standard hour: {{ starship.MGLT }}</p>
          <p>Cargo capacity: {{ starship.cargo_capacity }}</p>
          <p>Supplies enough for: {{ starship.consumables  }}</p>
        </div>
        <q-btn
          v-if="starship.inCart"
          @click="handleRemoveFromCart(starship)"
          outline
          color="info"
          label="Remove from cart" />
        <q-btn
          v-else
          @click="handleAddToCart(starship)"
          outline
          color="secondary"
          label="Add to cart" />
      </section>
    </q-card-section>
  </q-card>
</template>

<script>
  import { mapActions } from 'vuex';

  export default {
    name: 'DashboardItem',
    props: {
      starship: {
        type: Object,
        default: () => {},
      },
    },
    methods: {
      ...mapActions([
        'addToCart',
        'removeFromCart',
        'updateStarshipValue'
      ]),
      async handleAddToCart(starship) {
        await this.updateStarshipValue({ starship, inCart: true });
        await this.addToCart(starship);
        return null;
      },
      async handleRemoveFromCart(starship) {
        await this.updateStarshipValue({ starship, inCart: false });
        await this.removeFromCart(starship);
        return null;
      }
    },
  };
</script>

