<template>
  <div>
    <q-btn-group>
      <q-btn
        v-for="(button, index) in buttons"
        :key="index"
        @click="handleSortChange(button.value)"
        :outline="button.value === sortParam"
        :flat="button.value !== sortParam"
        color="secondary"
        :label="button.label" />
    </q-btn-group>
    <q-btn @click="handleClearSorting">Clear sort</q-btn>
  </div>
</template>

<script>
  import { mapActions, mapState } from 'vuex';

  export default {
    name: 'DashboardSort',
    computed: {
      ...mapState({
        sortParam: state => state.Dashboard.sortParam,
        sortDirection: state => state.Dashboard.sortDirection,
      }),
      buttons() {
        return [
          {
            label: 'Cost',
            value: 'cost_in_credits',
          },
          {
            label: 'Hyperdrive',
            value: 'hyperdrive_rating'
          },
          {
            label: 'Class',
            value: 'starship_class',
          },
        ];
      },
    },
    created() {
      const currentSortParam = localStorage.getItem('sortParam') ? localStorage.getItem('sortParam') : '';
      const currentDirection = localStorage.getItem('sortDirection') ? localStorage.getItem('sortDirection') : 'asc';
      this.setSorting(currentSortParam);
      this.setSortDirection(currentDirection);
    },
    methods: {
      ...mapActions([
        'setSorting',
        'clearSorting',
        'setSortDirection',
      ]),
      handleSortChange(value) {
        if (value !== this.sortParam) {
          this.setSorting(value);
          localStorage.setItem('sortParam', value);
        } else {
          const currentDirection = localStorage.getItem('sortDirection') || 'asc';
          this.setSortDirection(currentDirection === 'asc' ? 'desc' : 'asc');
          localStorage.setItem('sortDirection', currentDirection === 'asc' ? 'desc' : 'asc');
        }
      },
      handleClearSorting() {
        this.clearSorting();
        localStorage.setItem('sortParam', '');
        localStorage.setItem('sortDirection', 'asc');
      }
    },
  };
</script>

