<template>
  <q-input
    @input="handleInput"
    dark
    standout
    bottom-slots
    class="dashboard__search"
    label="Start typing...">
    <template v-slot:prepend>
      <q-icon name="search" />
    </template>
    <template v-slot:append>
      <q-icon
        name="close"
        @click="handleClearSearch"
        class="cursor-pointer" />
    </template>
  </q-input>
</template>

<script>
import { mapActions } from 'vuex';
  export default {
    name: 'DashboardSearch',
    methods: {
      ...mapActions([
        'setSearchQuery',
        'clearSearchQuery'
      ]),
      clearSearchField() {
        this.searchText = '';
      },
      handleInput(e) {
        this.setSearchQuery(e);
      },
      handleClearSearch() {
        this.clearSearchQuery();
      }
    },
  };
</script>

<style lang="scss" scoped>
  .dashboard__search {
    margin-bottom: 1.25rem;
  }
</style>
